import React, {useState} from 'react';
import axios from 'axios';
import Modal from 'react-modal';
import cartPhoto from './../assets/img/cart.webp'
import xx from './../assets/img/xx.webp'

Modal.setAppElement('#root'); //     

const Consolt = () => {
    const [customerName, setCustomerName] = useState('');
    const [customerPhone, setCustomerPhone] = useState(''); //  
    const [customerAddress, setCustomerAddress] = useState(''); //  
    const [modalIsOpen, setModalIsOpen] = useState(false);

    const openModal = () => setModalIsOpen(true);
    const closeModal = () => setModalIsOpen(false);

    const handleSubmit = async () => {
        const orderData = {
            customer_name: customerName,
            customer_phone: customerPhone, //  
            address: customerAddress,
            items: ""
        };
        try {
            const response = await axios.post('/api/order', orderData);
            if (response.data.message) {
                alert(` .  : ${response.data.orderId}`);
            }
        } catch (error) {
            alert(`  : ${error}`);
        }
    };

    return (<>
        <button onClick={openModal} className='banner__btn'>
            
        </button>Frj
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel=" "

        >
            <div onClick={closeModal} className='closeModal'>
                <img src={xx} alt=""/>
            </div>
            <div className="cart">
                <div className="cart__title">
                     
                </div>

                    <div className="order">

                        <label className="cart__search">
                            <input
                                type="text"
                                placeholder=":"
                                value={customerName}
                                onChange={e => setCustomerName(e.target.value)}
                            />
                        </label>
                        <label className="cart__search">
                            <input
                                type="text"
                                placeholder=":"
                                value={customerPhone}
                                onChange={e => setCustomerPhone(e.target.value)} //  
                            />
                        </label>
                        <label className="cart__search">
                            <input
                                type="text"
                                placeholder=":"
                                value={customerAddress}
                                onChange={e => setCustomerAddress(e.target.value)} //  
                            />
                        </label>
                        <button className="order__btn" onClick={handleSubmit}>
                            
                        </button>
                    </div>


            </div>
        </Modal>
    </>);
};

export default Consolt;
